@font-face {
  font-family: 'Poppins';
  src: url('../../themes/theme/assets/fonts/poppins/Poppins-Regular.ttf');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

h1 {
  font-size: var(--huge);
}

h2 {
  font-size: var(--large);
}

h3 {
  font-size: var(--medium);
}

h4 {
  font-size: var(--regular);
}